body{
    background-color:grey ;
}

.main{
    position: relative;
    top: 50px;
}

.main h1{
   font-size: 16px;
   font-family:'Times New Roman', Times, serif;
   font-weight: 700;
}

.balance-input-div{
    position: relative;
    background-color: gray;
}

.balance-input-div p{

    font-size: 40px;
    font-family: 'Times New Roman', Times, serif;
    letter-spacing: 3px;
}

.balance-input-div p img{
    position: relative;
    top: 8px;
   height: 45px;
   margin-right: 6px;
}

.amount-input{
    width: 70%;
    height: 25px;
}

.discription-input{
    width: 70%;
    height: 50px;
    margin-top: 20px;
    padding: 1%;
}

.buttons{
    display: flex; 
    justify-content: center;
}

.buttons p{
    position: relative;
    top: -15px;
    color: white;
    font-size: larger;
    font-family: 'Times New Roman', Times, serif;
}

.credit-button{
   height: 70px;
   width: 70px;
   border-radius: 100%;
   margin: 0 40px;
}

.credit-button img{
    height: 40px;
    transform: rotate(180deg);
}

.debit-button{
    height: 70px;
   width: 70px;
   border-radius: 100%;
   margin: 0 40px;
}

.debit-button img{
    height: 40px;
}



.transection-table thead tr th {
    position: relative;
    display: block;
    width: 370px;
    height: 20px;
    border-top: 1px solid black;
    border-bottom: 1px solid black;
    
}

.transection-table thead tr th p{
    position: relative;
    font-family:'Times New Roman', Times, serif ;
    font-size: 20px;
    font-weight: 900;
    letter-spacing: 1.5px;
    left: -110px;
    top: -22px;
}


.transection-table tbody{
        
    overflow-x: scroll;
}

.table-data{
    border-bottom: 1px solid black;
    height: 50px;
}


.table-data h1{
    position: relative;
    display: inline;
    color: black;
    font-family: 'Times New Roman', Times, serif;
    text-align: left;
    left: -70px;
    width: 250px;
    font-weight: 500;

}

.table-data h2{
    position: relative;
    display: inline;
    color: black;
    font-family: 'Times New Roman', Times, serif;
    text-align: left;
    font-size: 12px;
    width: 280px;
    top: 20px;
    right: 120px;
}

.table-data h3{
    position: relative;
    display: inline;
    color: black;
    font-family: 'Times New Roman', Times, serif;
    text-align: right;
    top: 13px;
    left: 65px;
}

@media only screen and (max-width: 2000px){
    .main{
        position: relative;
        width: 375px;
        
    }

    body{
        display: flex;
        justify-content: center;
        text-align: center;
        /* color: rgb(228, 224, 224); */
    }

    .table-body{
        overflow: scroll
    }

    
} 

